<template>
    <div>
		<el-card class="box-card" style="margin-top: 20px;">
		    <div class="setting-form">
		        <div>在线查题</div>
		        <el-input placeholder="请输入题目查题" v-model="searchText" class="input-with-select">
				 
		             <el-button slot="append" style="cursor:pointer ; " icon="el-icon-search" @click="queryTiMu()"></el-button>
		        </el-input>
		         <div v-if="searchQuestion!=''">题目：{{searchQuestion}}</div>
		        <div style="margin-top:20px" v-if="searchAnswer!=''">答案：{{searchAnswer}}</div>
		    </div>
		</el-card>
    </div>
</template>

<script>

    export default {
        data() {
            return {
				searchText: '', //题目搜索
				searchQuestion: '', //搜索到的题目
				searchAnswer: '', //搜索到的答案
			}
        },
        methods: {
			queryTiMu(){
			
				$.ajax({
					type: "get",
					url: originUrl + "/chati/" + chatiId + "/0/" + this.searchText,
					dataType: "json",
					success: r => {
						if (r.answer.indexOf("抱歉找不到结果") == -1) {
							this.searchQuestion = r.question
							this.searchAnswer = r.answer
						} else {
							this.searchAnswer = "抱歉找不到结果"
						}
			
			
					},
					error: e => {
						this.searchAnswer = "服务器错误"
					}
				});
			
			},
		},
        mounted() {
			
        },
    }
</script>

